import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import { Link } from 'react-router-dom';
import './Register.css';

type CreateUserInputs = {
  email: string;
  password: string;
};

const Register: React.FC = () => {
  const { register, handleSubmit, formState: { errors } } = useForm<CreateUserInputs>();
  const [message, setMessage] = useState('');

  const onSubmit = async (data: CreateUserInputs) => {
    try {
      const res = await fetch('http://localhost:4000/api/users', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data),
      });

      if (res.ok) {
        setMessage('   ');
      } else {
        const error = await res.json();
        setMessage(' : ' + error.message);
      }
    } catch (err) {
      console.error(err);
      setMessage('  ');
    }
  };

  return (
    <div className="auth-container">
      <form onSubmit={handleSubmit(onSubmit)} className="auth-form">
        <h2>  </h2>

        <div className="form-group">
          <label>:</label>
          <input type="email" {...register("email", { required: " " })} />
          {errors.email && <p className="error">{errors.email.message}</p>}
        </div>

        <div className="form-group">
          <label>:</label>
          <input type="password" {...register("password", { required: " " })} />
          {errors.password && <p className="error">{errors.password.message}</p>}
        </div>

        <button type="submit" className="auth-btn"> </button>

        {message && <p className={`message ${message.startsWith('') ? 'success' : 'error'}`}>{message}</p>}

        <p className="switch-auth">
             ? <Link to="/login"> </Link>
        </p>
      </form>
    </div>
  );
};

export default Register;
